import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import WebDevelopmentScreen from "@screens/website-development";

const WebDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Custom Web Development Company", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <WebDevelopmentScreen db={db} />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const fileUrl =
  data?.customWebDevImg?.childImageSharp?.gatsbyImageData?.images
    ?.fallback?.src;
  return (
    <Seo
      title="Custom Web Development Company | Justtry Technologies"
      metaTitle="Custom Web Application Development Company | Justtry Technologies"
      description="Grow your business with the help of our custom web application development services. We design unique solutions that meet your needs from start to finish."
      image={fileUrl}
    ></Seo>
  );
};

export default WebDevelopment;

export const queryData = graphql`
  query MyQuery {
    customWebDevImg: file(relativePath: { eq: "custom-web-development.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
